@-webkit-keyframes move {
	0% {
		width: 300px;
	}

	50% {
		width: 380px;
	}

	100% {
		width: 300px;
	}
}

@keyframes move {
	0% {
		width: 300px;
	}

	50% {
		width: 380px;
	}

	100% {
		width: 300px;
	}
}

@-webkit-keyframes hue-loader {
	0% {
		-webkit-filter: hue-rotate(-30deg);
		        filter: hue-rotate(-30deg);
	}

	50% {
		-webkit-filter: hue-rotate(140deg);
		        filter: hue-rotate(140deg);
	}

	100% {
		-webkit-filter: hue-rotate(-30deg);
		        filter: hue-rotate(-30deg);
	}
}

@keyframes hue-loader {
	0% {
		-webkit-filter: hue-rotate(-30deg);
		        filter: hue-rotate(-30deg);
	}

	50% {
		-webkit-filter: hue-rotate(140deg);
		        filter: hue-rotate(140deg);
	}

	100% {
		-webkit-filter: hue-rotate(-30deg);
		        filter: hue-rotate(-30deg);
	}
}

button {
	width: 300px;
	height: 30px;
	color: white;
	background: -webkit-linear-gradient(45deg, #695bcc, #ffdccb);
	background: -o-linear-gradient(45deg, #695bcc, #ffdccb);
	background: linear-gradient(45deg, #695bcc, #ffdccb);
	-webkit-animation: move 4s ease-out infinite;
	        animation: move 4s ease-out infinite;
	margin-top: 40px;
	margin-bottom: 20px;
	font-weight: 100;
	font-family: sans-serif;
	border: 0px;
	cursor: pointer;
}

button:hover {
	-webkit-animation: move 4s ease-out infinite, hue-loader 4s ease-out infinite;
	        animation: move 4s ease-out infinite, hue-loader 4s ease-out infinite;
}

button:focus, button:active {
	outline: none;
}

img {
	-webkit-box-shadow: 1px 1px 50px 6px #d7d5d5bf;
	        box-shadow: 1px 1px 50px 6px #d7d5d5bf;
	border: 32px solid white;
}

.doggosList {
	padding: 20px;
}

.doggosList__list {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}


