.App {
	text-align: center;
}

body {
	background: #eee;
}

.App-header {
	background: -webkit-linear-gradient(45deg, #ff7575, slateblue);
	background: -o-linear-gradient(45deg, #ff7575, slateblue);
	background: linear-gradient(45deg, #ff7575, slateblue);
	height: 110px;
	padding: 20px;
	color: white;
}

.App-title {
	text-transform: uppercase;
	font-weight: 300;
	text-shadow: 1px 1px 2px #525252;
	font-size: 42px;
}

.App-intro {
	font-size: large;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}



